@import url("https://kujitegemea.github.io/language-colors/language-colors.css");

.navbar{
  flex-direction: row;
  background-color: black;
  height:50px;
}

.nav-elem-group{
  display: flex;
  align-items: center;
}

.nav-elem:hover {
  transition: linear 0.2s;
  color:rgba(255,255,255,1);
}

.nav-elem {
  padding-right:10px ;
  list-style-type: none;
  color:rgba(255,255,255,0.75);
  cursor: default;
}

.nav-elem-reading-mode {
  width:30px;
  height:30px;
  padding-right:10px ;
  display: inline;
  list-style-type: none;
  color:rgba(255,255,255,0.75);
}

.reading-mode-icon{
  transition:linear 1s;
}

.download-button{
  background-color:#110d1a;
  color: #b197fc;
  width:120px;
  height:35px;
  border:0.5px solid #b197fc;
  border-radius:8px;
  margin-left:auto;
  transition:linear 0.2s; 
}

.download-button:hover{
  color:black;
  background-color: #b197fc;
  border:0.5px solid black;
}

.about-section{
  margin-left:20px;
}

.about-text{
  font-size:30px;
  color:white;
}

.project-card{
  border:0.1px white solid;
  border-radius: 0.75rem;
  width:500px;
  height:260px;
}

.project-section{
  padding:20px;
  row-gap:20px;
  width:57%;
  display:flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.project-link{
  margin-left:10px;
  display: block;
  font-weight: bolder;
  font-family: sans-serif;
  font-size:24px;
  background: linear-gradient(to right, #ec4899, #f97316, #facc15);
  background-clip:text;
  -webkit-text-fill-color: transparent;
}

.description{
  margin: 10px 10px;
  color:white;
}

.date{
  margin: 10px 10px;
  color:white;
}

.stars{
  margin: 10px 10px;
  color:white;
}

.skills-logo{
  width: 50px;
  height: 50px;
  transition: linear 0.2s;
}

.lang-used{
  color:white
}

.skills-logo:hover{
  transform: scale(1.5);
}

.heading{
  color:white;
}

.skills-section{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.image-stack{
  width:30%;
  height:100%;
  display: flex;
  column-gap: 25px;
  overflow: hidden;
  align-items: center;
}

.tech-stack-bar{
  width:50%;
  height: auto;
  display: flex;
  align-items:center;
  column-gap: 40px;
  justify-content: center;
  height:150px;
}

.scroll-button{
  width:auto;
  height: auto;
  background-color: transparent;
  border: none;
}

.arrow-top-left{
  display: block;
  width: 4px;
  height:20px;
  background-color:white;
  transform:rotate(45deg);
  transform-origin: bottom center;
}

.arrow-bottom-left{
  display: block;
  width:4px;
  height:20px;
  background-color:white;
  transform:rotate(-45deg);
  transform-origin: top center;
}

.arrow-top-right{
  display: block;
  width: 4px;
  height:20px;
  background-color:white;
  transform:rotate(-45deg);
  transform-origin: bottom center;
}

.arrow-bottom-right{
  display: block;
  width:4px;
  height:20px;
  background-color:white;
  transform:rotate(45deg);
  transform-origin: top center;
}

.language-head{
  margin-left: 10px;
  color:white;
}

.lang-display{
  color:white;
  list-style-type: none;
}

.langbar{
  margin-left:20px;
  
}

.langbar-section{
  display: inline-block;
}

.contact-section{
  width:100%;
  height:50px;
  display: flex;
  justify-content: center;
}

.contact-link{
  font-size: 30px;
  display: block;
  color:white;
  text-decoration: none;
}

.experience-section{
  width:auto;
  height: auto;
}

.experiences{
  color:white;
}

.heading-experience{
  color:white;
  margin-left: 20px;
}

.bullet{
  margin-right: 3px;
  display: inline-block;
  width:8px;
  height:8px;
  border-radius: 50%;
}